@font-face {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    src: local("Open Sans"),
        url(../../fonts/opensansbold.woff2) format("woff2"),
        url(../../fonts/opensansbold.woff) format("woff");
    font-display: swap;
}

@font-face {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 700;
    src: local("Open Sans"),
        url(../../fonts/opensansbold.woff2) format("woff2"),
        url(../../fonts/opensansbold.woff) format("woff");
    font-display: swap;
}
